import { Platform } from 'react-native';

interface PlausibleEvent {
  name: string;
  props?: Record<string, string | number | boolean>;
}

declare global {
  interface Window {
    plausible?: (eventName: string, options?: { props: Record<string, any> }) => void;
  }
}

export const trackEvent = (event: PlausibleEvent) => {
  if (Platform.OS === 'web' && window.plausible) {
    window.plausible(event.name, event.props ? { props: event.props } : undefined);
  }
};

export const trackPageView = (screenName: string, props?: Record<string, any>) => {
  trackEvent({
    name: 'pageview',
    props: {
      page: screenName,
      ...props
    }
  });
};

export const trackAction = (action: string, props?: Record<string, any>) => {
  trackEvent({
    name: action,
    props
  });
};

