import AsyncStorage from '@react-native-async-storage/async-storage';
import { logger } from './logger';
import { FilterOptions } from '../types/filters';
import { SortField } from '../hooks/useSortedFoils';

const STORAGE_KEYS = {
  FILTERS: '@foilquiver/filters',
  COLUMNS: '@foilquiver/columns',
  THEME: '@foilquiver/theme',
  SORT: '@foilquiver/sort',
} as const;

export interface StoredFilters {
  brands: string[];
  series: string[];
  types: string[];
}

export interface StoredColumns {
  [key: string]: boolean;
}

export interface StoredSort {
  field: SortField;
  ascending: boolean;
}

class Storage {
  async set(key: string, value: any): Promise<void> {
    try {
      const jsonValue = JSON.stringify(value);
      await AsyncStorage.setItem(key, jsonValue);
    } catch (error) {
      logger.error('Error setting storage item:', error);
      throw error;
    }
  }

  async get<T>(key: string): Promise<T | null> {
    try {
      const jsonValue = await AsyncStorage.getItem(key);
      return jsonValue != null ? JSON.parse(jsonValue) : null;
    } catch (error) {
      logger.error('Error getting storage item:', error);
      throw error;
    }
  }

  async remove(key: string): Promise<void> {
    try {
      await AsyncStorage.removeItem(key);
    } catch (error) {
      logger.error('Error removing storage item:', error);
      throw error;
    }
  }

  async clear(): Promise<void> {
    try {
      await AsyncStorage.clear();
    } catch (error) {
      logger.error('Error clearing storage:', error);
      throw error;
    }
  }

  async getAllKeys(): Promise<readonly string[]> {
    try {
      return await AsyncStorage.getAllKeys();
    } catch (error) {
      logger.error('Error getting all storage keys:', error);
      throw error;
    }
  }
}

export const storage = new Storage();

export const saveFilters = async (filters: StoredFilters) => {
  try {
    await AsyncStorage.setItem(STORAGE_KEYS.FILTERS, JSON.stringify(filters));
  } catch (error) {
    console.error('Error saving filters:', error);
  }
};

export const loadFilters = async (): Promise<StoredFilters | null> => {
  try {
    const savedFilters = await AsyncStorage.getItem(STORAGE_KEYS.FILTERS);
    return savedFilters ? JSON.parse(savedFilters) : null;
  } catch (error) {
    console.error('Error loading filters:', error);
    return null;
  }
};

