import React, { createContext, useContext, useState, useEffect, Dispatch, SetStateAction } from 'react';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';
import { Column } from '../constants/layout';
import { loadColumns, loadFilters, saveFilters } from '../utils/storage';
import { DEFAULT_COLUMNS } from '../constants/layout';

interface AppContextType {
  // Search state
  searchQuery: string;
  setSearchQuery: Dispatch<SetStateAction<string>>;
  
  // Filter state
  filters: FilterOptions;
  setFilters: Dispatch<SetStateAction<FilterOptions>>;
  
  // Column settings
  columns: Column[];
  setColumns: Dispatch<SetStateAction<Column[]>>;
  
  // Selected foils state
  selectedFoils: BaseProduct[];
  setSelectedFoils: Dispatch<SetStateAction<BaseProduct[]>>;
  
  // Navigation state
  currentScreen: string | null;
  setCurrentScreen: Dispatch<SetStateAction<string | null>>;
}

const AppContext = createContext<AppContextType | undefined>(undefined);

export const AppProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // Search state
  const [searchQuery, setSearchQuery] = useState('');
  
  // Filter state
  const [filters, setFilters] = useState<FilterOptions>({
    brands: [],
    series: [],
    type: []
  });
  
  // Column settings
  const [columns, setColumns] = useState<Column[]>(DEFAULT_COLUMNS);
  
  // Selected foils state
  const [selectedFoils, setSelectedFoils] = useState<BaseProduct[]>([]);
  
  // Navigation state - initialize as null to prevent default routing
  const [currentScreen, setCurrentScreen] = useState<string | null>(null);

  // Load saved filters and columns on mount
  useEffect(() => {
    const loadSavedSettings = async () => {
      try {
        const savedFilters = await loadFilters();
        if (savedFilters) {
          setFilters({
            brands: savedFilters.brands || [],
            series: savedFilters.series || [],
            type: savedFilters.types || []
          });
        }

        const savedColumns = await loadColumns();
        if (savedColumns && Array.isArray(savedColumns)) {
          setColumns(savedColumns.map((col: Partial<Column>) => ({
            ...DEFAULT_COLUMNS.find(defaultCol => defaultCol.id === col.id)!,
            ...col
          })));
        }
      } catch (error) {
        console.error('Error loading saved settings:', error);
      }
    };

    loadSavedSettings();
  }, []);

  // Save filters when they change
  useEffect(() => {
    saveFilters({
      brands: filters.brands,
      series: filters.series,
      types: filters.type
    });
  }, [filters]);

  // Reset filters when screen changes
  useEffect(() => {
    if (currentScreen) {
      setFilters({ brands: [], series: [], type: [] });
    }
  }, [currentScreen]);

  const value = {
    searchQuery,
    setSearchQuery,
    filters,
    setFilters,
    columns,
    setColumns,
    selectedFoils,
    setSelectedFoils,
    currentScreen,
    setCurrentScreen,
  };

  return (
    <AppContext.Provider value={value}>
      {children}
    </AppContext.Provider>
  );
};

export const useApp = () => {
  const context = useContext(AppContext);
  if (context === undefined) {
    throw new Error('useApp must be used within an AppProvider');
  }
  return context;
}; 