import { Platform } from 'react-native';

export type Environment = 'development' | 'staging' | 'production';

interface EnvironmentConfig {
  apiUrl: string;
  webUrl: string;
  appScheme: string;
}

const ENV: Environment = process.env.EXPO_PUBLIC_ENV as Environment || 'development';

const configs: Record<Environment, EnvironmentConfig> = {
  development: {
    apiUrl: 'http://localhost:3000',
    webUrl: 'http://localhost:19006',
    appScheme: 'foilquiver-dev',
  },
  staging: {
    apiUrl: 'https://api.staging.foilquiver.com',
    webUrl: 'https://staging.foilquiver.com',
    appScheme: 'foilquiver-staging',
  },
  production: {
    apiUrl: 'https://api.foilquiver.com',
    webUrl: 'https://foilquiver.com',
    appScheme: 'foilquiver',
  },
};

export const getEnvironmentConfig = (): EnvironmentConfig => {
  return configs[ENV];
};

export const getPrefixes = (): string[] => {
  const config = getEnvironmentConfig();
  if (Platform.OS === 'web') {
    return [config.webUrl];
  }
  return [`${config.appScheme}://`];
};

export const isProduction = () => ENV === 'production';
export const isDevelopment = () => ENV === 'development';
export const isStaging = () => ENV === 'staging'; 