import React from 'react';
import { TouchableOpacity, StyleSheet, Text, ActivityIndicator, Alert } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { colors } from '../theme/colors';
import { useGoogleAuth } from '../hooks/useGoogleAuth';
import { AuthError } from '@supabase/supabase-js';

interface GoogleSignInProps {
  onSuccess?: () => void;
}

export const GoogleSignIn = ({ onSuccess }: GoogleSignInProps) => {
  const { signInWithGoogle, isLoading } = useGoogleAuth();

  const handleSignIn = async () => {
    try {
      const { error } = await signInWithGoogle();
      
      if (error) {
        Alert.alert('Sign In Error', error instanceof AuthError ? error.message : 'Failed to sign in');
        return;
      }

      onSuccess?.();
    } catch (error) {
      Alert.alert('Error', 'An unexpected error occurred during sign in.');
      console.error('Sign in error:', error);
    }
  };

  return (
    <TouchableOpacity
      style={styles.button}
      onPress={handleSignIn}
      disabled={isLoading}
    >
      {isLoading ? (
        <ActivityIndicator color={colors.text.inverse} />
      ) : (
        <>
          <Ionicons name="logo-google" size={24} color={colors.text.inverse} style={styles.icon} />
          <Text style={styles.buttonText}>Sign in with Google</Text>
        </>
      )}
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: colors.primary,
    padding: 12,
    borderRadius: 8,
    minWidth: 220,
  },
  buttonText: {
    color: colors.text.inverse,
    fontSize: 16,
    fontWeight: '600',
  },
  icon: {
    marginRight: 8,
  },
}); 