import { BaseProduct } from '../types/scraper';

export const generateFoilId = (foil: BaseProduct): string => {
  return `${foil.brand.toLowerCase()}-${foil.model.toLowerCase().replace(/\s+/g, '-')}`;
};

// Generate a UUID v4 that works across platforms
export const generateUUID = (): string => {
  // Use crypto.randomUUID if available (web)
  if (typeof crypto !== 'undefined' && crypto.randomUUID) {
    return crypto.randomUUID();
  }

  // Fallback implementation for mobile
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
    const r = Math.random() * 16 | 0;
    const v = c === 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}; 