import React, { lazy, Suspense, useEffect, useMemo, useContext } from 'react';
import { View, Text, StyleSheet, Pressable, Platform, useWindowDimensions, Alert, TouchableOpacity } from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { useNavigation } from '@react-navigation/native';
import { RootStackParamList, RootStackNavigationProp } from '../types/navigation';
import { SearchBar } from '../components/SearchBar';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { TableSettings } from '../components/TableSettings';
import { useTheme } from '../theme/ThemeContext';
import { useFoils } from '../hooks/useFoils';
import { useFilteredFoils } from '../hooks/useFilteredFoils';
import { FilterOptions } from '../types/filters';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT, DEFAULT_COLUMNS, getTableWidth, getContentWidth, Column } from '../constants/layout';
import { loadColumns, loadFilters, saveFilters } from '../utils/storage';
import { Ionicons } from '@expo/vector-icons';
import { useApp } from '../context/AppContext';
import { routes } from '../utils/routes';
import { getEnvironmentConfig } from '../config/environment';
import { generateFoilId } from '../utils/foilUtils';
import { QuiverManager } from '../components/QuiverManager';
import { QuiverContext } from '../../App';

// Lazy load screens with proper suspense boundaries
const BrowseScreen = React.lazy(() => import('../screens/BrowseScreen'));
const ComparisonScreen = React.lazy(() => import('../screens/ComparisonScreen'));
const QuiverScreen = React.lazy(() => import('../screens/QuiverScreen'));
const PrivacyPolicy = React.lazy(() => import('../screens/legal/PrivacyPolicy'));
const TermsOfService = React.lazy(() => import('../screens/legal/TermsOfService'));
const Contact = React.lazy(() => import('../screens/legal/Contact'));

const Stack = createNativeStackNavigator<RootStackParamList>();

const ICON_NAMES = {
  home: 'home-outline' as const,
  browse: 'search-outline' as const,
  compare: 'git-compare-outline' as const,
  settings: 'settings-outline' as const,
  quiver: 'bookmark-outline' as const,
};

function TabBarIcon({ name, color }: { name: keyof typeof ICON_NAMES; color: string }) {
  if (Platform.OS === 'web') {
    // Use text alternatives for web
    const webIcons: Record<keyof typeof ICON_NAMES, string> = {
      home: '🏠',
      browse: '🔍',
      compare: '⚖️',
      settings: '⚙️',
      quiver: '🔖'
    };
    return <Text style={{ fontSize: 20, color }}>{webIcons[name]}</Text>;
  }
  return <Ionicons name={ICON_NAMES[name]} size={24} color={color} />;
}

// Define linking configuration for web
const linking = {
  prefixes: [getEnvironmentConfig().webUrl],
  config: {
    screens: {
      Browse: {
        path: routes.browse,
        parse: {
          mode: (mode: string) => mode,
        },
      },
      Comparison: routes.comparison,
      Quiver: routes.quiver,
      Terms: routes.terms,
      Privacy: routes.privacy,
      Contact: routes.contact,
    },
  },
};

// Screen wrapper with suspense and navigation props
const ScreenWrapper = ({ 
  children, 
  ...props 
}: { 
  children: React.ReactElement; 
  [key: string]: any;
}) => (
  <Suspense fallback={<LoadingIndicator />}>
    {React.cloneElement(children, props)}
  </Suspense>
);

export const AppHeader = () => {
  const navigation = useNavigation<RootStackNavigationProp>();
  const { colors } = useTheme();
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;
  const { foils } = useFoils();
  const { 
    searchQuery, 
    setSearchQuery, 
    filters, 
    setFilters, 
    columns, 
    setColumns,
    setCurrentScreen 
  } = useApp();
  const { filterOptions } = useFilteredFoils(foils, filters);
  const { showQuiverManager } = useContext(QuiverContext);

  // Calculate brands from foils
  const brands = React.useMemo(() => {
    if (!foils?.length) return [];
    return [...new Set(foils.map(foil => foil.brand))].sort();
  }, [foils]);

  // Add missing function definitions
  const handleLogoPress = () => {
    navigation.navigate('Browse', {});
  };

  const handleSearch = (text: string) => {
    setSearchQuery(text);
    navigation.setParams({ searchQuery: text });
  };

  const getViewMode = (route: any) => {
    if (route.name === 'Comparison') return 'comparison';
    if (route.name === 'Quiver') return 'quiver';
    return 'browse';
  };

  const getSearchPlaceholder = (route: any) => {
    switch (route.name) {
      case 'Comparison':
        return 'Search foils to add to comparison...';
      case 'Quiver':
        return 'Search foils to add to quiver...';
      default:
        return 'Search foils...';
    }
  };

  const handleSearchFocus = (route: any) => {
    if (route.name === 'Browse') {
      navigation.setParams({ showSearchDropdown: true });
    }
  };

  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => ({
      ...prev,
      [type]: prev[type].includes(value)
        ? prev[type].filter(item => item !== value)
        : [...prev[type], value]
    }));
  };

  const handleFilterChangeWrapper = (type: string, value: string) => {
    handleFilterChange(type as keyof FilterOptions, value);
  };

  const handleClearFilters = () => {
    setFilters({
      brands: [],
      series: [],
      type: []
    });
  };

  const handleColumnVisibilityChange = (columnId: string) => {
    setColumns(prev => 
      prev.map(col => 
        col.id === columnId ? { ...col, isVisible: !col.isVisible } : col
      )
    );
  };

  const handleResetColumns = () => {
    setColumns(DEFAULT_COLUMNS);
  };

  // Calculate table width based on visible columns
  const tableWidth = React.useMemo(() => getTableWidth(columns), [columns]);

  // Calculate content width including padding
  const contentWidth = React.useMemo(() => 
    getContentWidth(tableWidth, width, isMobile), 
    [tableWidth, width, isMobile]
  );

  // Calculate container width for header alignment
  const containerWidth = React.useMemo(() => {
    if (isMobile) {
      return '100%';
    }
    // Match the table's container width exactly
    return Math.min(tableWidth + (CONTAINER_PADDING * 2), width);
  }, [tableWidth, width, isMobile]);

  // Load saved column settings on mount
  useEffect(() => {
    const loadSavedColumnSettings = async () => {
      try {
        const savedColumns = await loadColumns();
        if (savedColumns) {
          const updatedColumns = DEFAULT_COLUMNS.map(col => ({
            ...col,
            isVisible: typeof savedColumns[col.id] === 'boolean' ? savedColumns[col.id] : col.isVisible
          }));
          setColumns(updatedColumns);
        }
      } catch (error) {
        console.error('Error loading column settings:', error);
      }
    };

    loadSavedColumnSettings();
  }, []);

  // Load saved filter settings on mount
  useEffect(() => {
    const loadSavedSettings = async () => {
      try {
        const savedFilters = await loadFilters();
        if (savedFilters) {
          setFilters({
            brands: savedFilters.brands || [],
            series: savedFilters.series || [],
            type: savedFilters.types || []
          });
        }
      } catch (error) {
        console.error('Error loading filter settings:', error);
      }
    };

    loadSavedSettings();
  }, []);

  const headerMainContainerStyle = React.useMemo(() => {
    if (isMobile) {
      return {
        width: '100%' as const,
        paddingHorizontal: CONTAINER_PADDING
      };
    }

    // For web, match the table width exactly without additional padding
    const containerWidth = getTableWidth(columns);
    return {
      width: containerWidth,
      maxWidth: getContentWidth(containerWidth, width, false),
      alignSelf: 'center' as const
    };
  }, [columns, width, isMobile]);

  const renderHeaderRight = (route: any) => {
    if (isMobile) {
      return (
        <View style={styles.headerRightContainerMobile}>
          <TouchableOpacity
            style={[styles.headerButton]}
            onPress={showQuiverManager}
          >
            <TabBarIcon name="quiver" color={colors.text.primary} />
          </TouchableOpacity>
          <TableSettings
            filterOptions={filterOptions}
            selectedBrands={filters.brands}
            selectedSeries={filters.series}
            selectedTypes={filters.type}
            onFilterChange={handleFilterChangeWrapper}
            onClearFilters={handleClearFilters}
            columns={columns}
            onColumnVisibilityChange={handleColumnVisibilityChange}
            onResetColumns={handleResetColumns}
            foils={foils}
          />
        </View>
      );
    }

    return (
      <View style={styles.headerRightContainerWeb}>
        <TouchableOpacity
          style={[styles.headerButton]}
          onPress={showQuiverManager}
        >
          <TabBarIcon name="quiver" color={colors.text.primary} />
          {!isMobile && (
            <Text style={[styles.headerButtonText, { color: colors.text.primary }]}>
              My Quivers
            </Text>
          )}
        </TouchableOpacity>
        <TableSettings
          filterOptions={filterOptions}
          selectedBrands={filters.brands}
          selectedSeries={filters.series}
          selectedTypes={filters.type}
          onFilterChange={handleFilterChangeWrapper}
          onClearFilters={handleClearFilters}
          columns={columns}
          onColumnVisibilityChange={handleColumnVisibilityChange}
          onResetColumns={handleResetColumns}
          foils={foils}
        />
      </View>
    );
  };

  return (
    <Stack.Navigator
      screenOptions={{
        headerStyle: {
          backgroundColor: colors.background.primary,
        },
        headerShadowVisible: false,
      }}
    >
      <Stack.Screen 
        name="Browse"
        options={({ route }) => ({
          headerTitle: route.params?.title || '',
          headerLeft: () => (
            <View style={[
              styles.headerOuterContainer,
              { backgroundColor: colors.background.primary },
              !isMobile && { alignItems: 'center' }
            ]}>
              {isMobile ? (
                // Mobile Layout
                <View style={styles.headerMainContainerMobile}>
                  <View style={styles.headerTopRowMobile}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainerMobile,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitleMobile, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={styles.searchBarContainerMobile}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              ) : (
                // Web Layout
                <View style={[
                  styles.headerMainContainerWeb,
                  headerMainContainerStyle
                ]}>
                  <View style={[
                    styles.headerContentWeb,
                    { 
                      width: '100%',
                      alignItems: 'center',
                      justifyContent: 'space-between'
                    }
                  ]}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainer,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitle, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={[styles.searchBarContainerWeb, { flex: 1 }]}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              )}
            </View>
          ),
        })}
      >
        {(props) => (
          <ScreenWrapper>
            <BrowseScreen
              {...props}
              filters={filters}
              onFilterChange={handleFilterChangeWrapper}
              columns={columns}
              brands={brands}
            />
          </ScreenWrapper>
        )}
      </Stack.Screen>

      <Stack.Screen
        name="Comparison"
        options={({ route }) => ({
          headerTitle: route.params?.title || '',
          headerLeft: () => (
            <View style={[
              styles.headerOuterContainer,
              { backgroundColor: colors.background.primary },
              !isMobile && { alignItems: 'center' }
            ]}>
              {isMobile ? (
                // Mobile Layout
                <View style={styles.headerMainContainerMobile}>
                  <View style={styles.headerTopRowMobile}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainerMobile,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitleMobile, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={styles.searchBarContainerMobile}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              ) : (
                // Web Layout
                <View style={[
                  styles.headerMainContainerWeb,
                  headerMainContainerStyle
                ]}>
                  <View style={[
                    styles.headerContentWeb,
                    { 
                      width: '100%',
                      alignItems: 'center',
                      justifyContent: 'space-between'
                    }
                  ]}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainer,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitle, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={[styles.searchBarContainerWeb, { flex: 1 }]}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              )}
            </View>
          ),
        })}
      >
        {(props) => (
          <ScreenWrapper>
            <ComparisonScreen {...props} />
          </ScreenWrapper>
        )}
      </Stack.Screen>

      <Stack.Screen
        name="Quiver"
        options={({ route }) => ({
          headerTitle: '',
          headerLeft: () => (
            <View style={[
              styles.headerOuterContainer,
              { backgroundColor: colors.background.primary },
              !isMobile && { alignItems: 'center' }
            ]}>
              {isMobile ? (
                // Mobile Layout
                <View style={styles.headerMainContainerMobile}>
                  <View style={styles.headerTopRowMobile}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainerMobile,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitleMobile, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={styles.searchBarContainerMobile}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              ) : (
                // Web Layout
                <View style={[
                  styles.headerMainContainerWeb,
                  headerMainContainerStyle
                ]}>
                  <View style={[
                    styles.headerContentWeb,
                    { 
                      width: '100%',
                      alignItems: 'center',
                      justifyContent: 'space-between'
                    }
                  ]}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainer,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitle, { color: colors.primary }]}>Foilquiver</Text>
                    </Pressable>
                    <View style={[styles.searchBarContainerWeb, { flex: 1 }]}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={(text) => handleSearch(text)}
                        onClear={() => handleSearch('')}
                        mode={getViewMode(route)}
                        placeholder={getSearchPlaceholder(route)}
                        onFocus={() => handleSearchFocus(route)}
                      />
                    </View>
                    {renderHeaderRight(route)}
                  </View>
                </View>
              )}
            </View>
          ),
        })}
      >
        {(props) => {
          const currentQuiverFoils = React.useMemo(() => {
            if (!props.route.params?.foil_ids || !Array.isArray(foils)) return [];
            return foils.filter(f => {
              if (!f) return false;
              const foilId = generateFoilId(f);
              return props.route.params.foil_ids.includes(foilId);
            });
          }, [props.route.params?.foil_ids, foils]);

          return (
            <ScreenWrapper>
              <QuiverScreen {...props}
                searchProps={{
                  value: searchQuery,
                  onChangeText: handleSearch,
                  onClear: () => setSearchQuery(''),
                  mode: 'quiver',
                  placeholder: 'Search foils in quiver...'
                }}
                tableSettingsProps={{
                  filterOptions,
                  selectedBrands: filters.brands,
                  selectedSeries: filters.series,
                  selectedTypes: filters.type,
                  onFilterChange: handleFilterChangeWrapper,
                  onClearFilters: handleClearFilters,
                  columns,
                  onColumnVisibilityChange: handleColumnVisibilityChange,
                  onResetColumns: handleResetColumns,
                  foils: currentQuiverFoils
                }}
              />
            </ScreenWrapper>
          );
        }}
      </Stack.Screen>

      <Stack.Screen
        name="Terms"
        options={{
          headerShown: false,
        }}
      >
        {(props) => (
          <ScreenWrapper>
            <TermsOfService {...props} />
          </ScreenWrapper>
        )}
      </Stack.Screen>

      <Stack.Screen
        name="Privacy"
        options={{
          headerShown: false,
        }}
      >
        {(props) => (
          <ScreenWrapper>
            <PrivacyPolicy {...props} />
          </ScreenWrapper>
        )}
      </Stack.Screen>

      <Stack.Screen
        name="Contact"
        options={{
          headerShown: false,
        }}
      >
        {(props) => (
          <ScreenWrapper>
            <Contact {...props} />
          </ScreenWrapper>
        )}
      </Stack.Screen>
    </Stack.Navigator>
  );
};

const styles = StyleSheet.create({
  headerOuterContainer: {
    flex: 1,
    paddingHorizontal: CONTAINER_PADDING,
  },
  // Mobile Styles
  headerMainContainerMobile: {
    width: '100%',
  },
  headerTopRowMobile: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    height: 48,
    width: '100%',
    gap: 8,
  },
  logoContainerMobile: {
    height: '100%',
    justifyContent: 'center',
    minWidth: 80,
  },
  headerTitleMobile: {
    fontSize: 18,
    fontWeight: 'bold',
  },
  searchBarContainerMobile: {
    flex: 1,
    height: 36,
    minWidth: 120,
    maxWidth: 200,
  },
  headerRightContainerMobile: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 4,
    zIndex: 2000,
  },
  // Web Styles
  headerMainContainerWeb: {
    height: 72,
    width: '100%',
  },
  headerContentWeb: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 24,
    height: '100%',
    width: '100%',
  },
  searchBarContainerWeb: {
    flex: 1,
    height: 40,
  },
  // Common Styles
  logoContainer: {
    height: '100%',
    justifyContent: 'center',
    minWidth: 120,
  },
  logoPressed: {
    opacity: 0.7,
  },
  headerTitle: {
    fontSize: 24,
    fontWeight: 'bold',
  },
  headerRightContainerWeb: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 16,
    paddingRight: CONTAINER_PADDING,
    zIndex: 2000,
  },
  headerButton: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 8,
    padding: 6,
    borderRadius: 8,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      userSelect: 'none',
    }),
  },
  headerButtonText: {
    fontSize: 14,
    fontWeight: '500',
  },
}); 