import { useState, useEffect } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Platform } from 'react-native';
import { supabase } from '../utils/supabase';
import { Quiver, CreateQuiverInput, UpdateQuiverInput } from '../types/quiver';
import { BaseProduct } from '../types/scraper';
import { generateFoilId, generateUUID } from '../utils/foilUtils';
import { logger } from '../utils/logger';
import { Environment } from '../config/environment';

const STORAGE_KEYS = {
  QUIVERS: '@foilquiver/quivers',
} as const;

// Get the current environment
const getEnvironment = (): 'dev' | 'staging' | 'prod' => {
  if (process.env.NODE_ENV === 'development') return 'dev';
  if (window.location.hostname.includes('staging')) return 'staging';
  return 'prod';
};

// Get the table name for the current environment
const getTableName = () => {
  const env = getEnvironment();
  return `${env}_quivers`;
};

// Storage helper that works for both web and native
const storage = {
  getItem: async (key: string): Promise<string | null> => {
    if (Platform.OS === 'web') {
      return localStorage.getItem(key);
    }
    return AsyncStorage.getItem(key);
  },
  setItem: async (key: string, value: string): Promise<void> => {
    if (Platform.OS === 'web') {
      localStorage.setItem(key, value);
      return;
    }
    return AsyncStorage.setItem(key, value);
  },
  removeItem: async (key: string): Promise<void> => {
    if (Platform.OS === 'web') {
      localStorage.removeItem(key);
      return;
    }
    return AsyncStorage.removeItem(key);
  }
};

// Local interface for creating quivers
interface LocalCreateQuiverInput {
    name: string;
    description?: string;
    foils: BaseProduct[];
    is_public?: boolean;
}

// Local interface for syncing quivers
interface LocalQuiverData {
    name: string;
    description?: string;
    foils: BaseProduct[];
    is_public?: boolean;
}

const ENV: Environment = process.env.EXPO_PUBLIC_ENV as Environment || 'development';

export const useQuivers = () => {
  const [quivers, setQuivers] = useState<Quiver[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadQuivers = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();
      
      if (session?.user) {
        const { data, error: supabaseError } = await supabase
          .from(getTableName())
          .select('*')
          .eq('environment', getEnvironment())
          .eq('user_id', session.user.id)
          .order('updated_at', { ascending: false });

        if (supabaseError) throw supabaseError;
        setQuivers(data || []);
      } else {
        const storedQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        if (storedQuivers) {
          setQuivers(JSON.parse(storedQuivers));
        } else {
          setQuivers([]);
        }
      }
    } catch (err) {
      console.error('Error loading quivers:', err);
      setError(err instanceof Error ? err.message : 'Failed to load quivers');
      setQuivers([]);
    } finally {
      setIsLoading(false);
    }
  };

  const createQuiver = async (input: LocalCreateQuiverInput): Promise<Quiver | null> => {
    try {
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();

      if (session?.user) {
        const tableName = getTableName();
        const env = getEnvironment();

        const foil_ids = input.foils.map(foil => generateFoilId(foil));

        const insertData = {
          name: input.name,
          description: input.description || '',
          user_id: session.user.id,
          environment: env,
          foil_ids,
          is_public: input.is_public || false
        };

        const { error: insertError } = await supabase
          .from(tableName)
          .insert(insertData);

        if (insertError) {
          logger.error('Insert error:', {
            message: insertError.message,
            details: insertError.details,
            hint: insertError.hint,
            code: insertError.code
          });
          throw insertError;
        }

        const { data: newData, error: selectError } = await supabase
          .from(tableName)
          .select('*')
          .eq('user_id', session.user.id)
          .eq('name', input.name)
          .order('created_at', { ascending: false })
          .limit(1)
          .single();

        if (selectError) throw selectError;
        if (!newData) throw new Error('Failed to retrieve created quiver');

        const newQuiver = {
          ...newData,
          foils: input.foils
        };

        setQuivers(prev => [newQuiver, ...prev]);
        return newQuiver;
      } else {
        const env = getEnvironment();
        const foil_ids = input.foils.map(foil => generateFoilId(foil));
        
        const newQuiver: Quiver = {
          id: generateUUID(),
          name: input.name,
          description: input.description || '',
          foil_ids,
          is_public: input.is_public || false,
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString(),
          user_id: 'local',
          environment: env
        };

        const updatedQuivers = [newQuiver, ...quivers];
        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
        setQuivers(updatedQuivers);
        return newQuiver;
      }
    } catch (err) {
      console.error('Error creating quiver:', err);
      throw err;
    }
  };

  const deleteQuiver = async (id: string): Promise<boolean> => {
    try {
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();

      if (session?.user) {
        const { error: supabaseError } = await supabase
          .from(getTableName())
          .delete()
          .eq('id', id)
          .eq('user_id', session.user.id)
          .eq('environment', getEnvironment());

        if (supabaseError) throw supabaseError;
      }

      const updatedQuivers = quivers.filter(quiver => quiver.id !== id);
      setQuivers(updatedQuivers);

      if (!session?.user) {
        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
      }

      return true;
    } catch (err) {
      console.error('Error deleting quiver:', err);
      setError(err instanceof Error ? err.message : 'Failed to delete quiver');
      return false;
    }
  };

  // Update an existing quiver
  const updateQuiver = async (quiverId: string, updates: UpdateQuiverInput) => {
    setError(null);
    try {
      const { data: { session } } = await supabase.auth.getSession();
      
      if (session?.user) {
        const { error } = await supabase
          .from(getTableName())
          .update(updates)
          .eq('id', quiverId);

        if (error) throw error;
        await loadQuivers();
        return true;
      } else {
        // Local storage update logic
        const storedQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        if (!storedQuivers) return false;

        const parsedQuivers: Quiver[] = JSON.parse(storedQuivers);
        const updatedQuivers = parsedQuivers.map(quiver => {
          if (quiver.id === quiverId) {
            return {
              ...quiver,
              ...updates,
              updated_at: new Date().toISOString()
            };
          }
          return quiver;
        });

        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
        setQuivers(updatedQuivers);
        return true;
      }
    } catch (error) {
      console.error('Error updating quiver:', error);
      setError('Failed to update quiver');
      return false;
    }
  };

  // Sync storage quivers to Supabase when user logs in
  const syncQuiversToSupabase = async () => {
    try {
      const { data: { session } } = await supabase.auth.getSession();
      if (!session?.user) return;

      const localQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
      if (!localQuivers) return;

      const parsedQuivers: LocalQuiverData[] = JSON.parse(localQuivers);
      const env = getEnvironment();
      
      // Insert all local quivers to Supabase
      for (const quiver of parsedQuivers) {
        const foil_ids = quiver.foils.map(foil => generateFoilId(foil));
        const insertData = {
          name: quiver.name,
          description: quiver.description || '',
          foil_ids,
          is_public: quiver.is_public || false,
          user_id: session.user.id,
          environment: env
        };
        await supabase
          .from(getTableName())
          .insert([insertData]);
      }

      // Clear storage after successful sync
      await storage.removeItem(STORAGE_KEYS.QUIVERS);
      
      // Reload quivers from Supabase
      await loadQuivers();
    } catch (err) {
      console.error('Error syncing quivers:', err);
    }
  };

  // Load quivers on mount and when auth state changes
  useEffect(() => {
    loadQuivers();

    // Listen for auth state changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (event) => {
      if (event === 'SIGNED_IN') {
        await syncQuiversToSupabase();
      }
      await loadQuivers();
    });

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  return {
    quivers,
    isLoading,
    error,
    createQuiver,
    updateQuiver,
    deleteQuiver,
    reloadQuivers: loadQuivers,
  };
}; 