// Layout constants
export const CONTAINER_PADDING = 16;
export const MAX_CONTENT_WIDTH = 1200;
export const MOBILE_BREAKPOINT = 768;

// Column width constants
export const COLUMN_WIDTHS = {
  brand: 100,
  series: 120,
  model: 150,
  area: 80,
  wingspan: 80,
  aspectRatio: 80,
  type: 80,
  price: 105,
  link: 80,
} as const;

export type Column = {
  id: keyof typeof COLUMN_WIDTHS;
  label: string;
  isVisible: boolean;
};

// Default column configuration
export const DEFAULT_COLUMNS: Column[] = [
  { id: 'brand', label: 'Brand', isVisible: true },
  { id: 'series', label: 'Series', isVisible: false },
  { id: 'model', label: 'Model', isVisible: true },
  { id: 'area', label: 'Area', isVisible: true },
  { id: 'wingspan', label: 'Span', isVisible: true },
  { id: 'aspectRatio', label: 'AR', isVisible: true },
  { id: 'type', label: 'Type', isVisible: true },
  { id: 'price', label: 'Price', isVisible: true },
  { id: 'link', label: 'Link', isVisible: true },
];

// Helper functions for width calculations
export const getTableWidth = (columns: Column[]) => {
  return columns.reduce((total, column) => {
    if (column.isVisible) {
      return total + (COLUMN_WIDTHS[column.id] || 100);
    }
    return total;
  }, 0);
};

export const getContentWidth = (tableWidth: number, screenWidth: number, isMobile: boolean) => {
  if (isMobile) {
    return screenWidth;
  }
  return Math.min(tableWidth, screenWidth - 48);
}; 