import { ColorSchemeName } from 'react-native';

export type ThemeColors = {
  background: {
    primary: string;
    secondary: string;
    tertiary: string;
  };
  text: {
    primary: string;
    secondary: string;
    tertiary: string;
    inverse: string;
  };
  border: string;
  primary: string;
  error: string;
  success: string;
  warning: string;
};

// Sunset theme colors (accent colors)
const SPACE_CADET = '#1F214D';
const EMINENCE = '#50366F';
const MAGENTA_DYE = '#BF3475';
const BURNT_SIENNA = '#EE6C45';
const SUNGLOW = '#FFCE61';

// Neutral colors for text and backgrounds
const NEUTRAL = {
  50: '#F9FAFB',
  100: '#F3F4F6',
  200: '#E5E7EB',
  300: '#D1D5DB',
  400: '#9CA3AF',
  500: '#6B7280',
  600: '#4B5563',
  700: '#374151',
  800: '#1F2937',
  900: '#111827',
};

const lightColors: ThemeColors = {
  background: {
    primary: NEUTRAL[50],
    secondary: NEUTRAL[100],
    tertiary: NEUTRAL[200],
  },
  text: {
    primary: NEUTRAL[900],
    secondary: NEUTRAL[700],
    tertiary: NEUTRAL[500],
    inverse: NEUTRAL[50],
  },
  border: NEUTRAL[200],
  primary: MAGENTA_DYE,
  error: '#DC2626',
  success: '#059669',
  warning: SUNGLOW,
};

const darkColors: ThemeColors = {
  background: {
    primary: NEUTRAL[900],
    secondary: NEUTRAL[800],
    tertiary: NEUTRAL[700],
  },
  text: {
    primary: NEUTRAL[50],
    secondary: NEUTRAL[200],
    tertiary: NEUTRAL[400],
    inverse: NEUTRAL[900],
  },
  border: NEUTRAL[700],
  primary: MAGENTA_DYE,
  error: '#EF4444',
  success: '#10B981',
  warning: SUNGLOW,
};

export const getColors = (colorScheme: ColorSchemeName): ThemeColors => {
  return colorScheme === 'dark' ? darkColors : lightColors;
};

// For backward compatibility
export const colors = lightColors; 

