import React, { useState, useMemo, useEffect } from 'react';
import { View, Text, Pressable, StyleSheet, Modal, Platform, ScrollView, Image, Alert } from 'react-native';
import { MaterialIcons, Ionicons } from '@expo/vector-icons';
import { useTheme } from '../theme/ThemeContext';
import { FilterOptions } from '../types/filters';
import { BaseProduct } from '../types/scraper';
import { saveFilters, saveColumns, loadFilters, loadColumns, StoredFilters, StoredColumns } from '../utils/storage';
import { spacing } from '../theme/spacing';
import { supabase } from '../utils/supabase';
import { Session, User } from '@supabase/supabase-js';
import { GoogleSignIn } from './GoogleSignIn';
import { QuiversMenu } from './QuiversMenu';
import { QuiverManager } from './QuiverManager';

interface Column {
  id: string;
  label: string;
  isVisible: boolean;
}

interface TableSettingsProps {
  filterOptions: {
    brands: string[];
    series: string[];
    types: string[];
  };
  selectedBrands: string[];
  selectedSeries: string[];
  selectedTypes: string[];
  onFilterChange: (type: keyof FilterOptions, value: string) => void;
  onClearFilters: () => void;
  columns: Column[];
  onColumnVisibilityChange: (columnId: string) => void;
  onResetColumns: () => void;
  foils: BaseProduct[];
}

export function TableSettings({
  filterOptions,
  selectedBrands,
  selectedSeries,
  selectedTypes,
  onFilterChange,
  onClearFilters,
  columns,
  onColumnVisibilityChange,
  onResetColumns,
  foils,
}: TableSettingsProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [activeTab, setActiveTab] = useState<'filters' | 'columns' | 'account' | 'appearance'>('filters');
  const { colors, isDark, toggleTheme } = useTheme();
  const [session, setSession] = useState<Session | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [isQuiversModalVisible, setIsQuiversModalVisible] = useState(false);

  // Get initial session and listen for auth changes
  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      setUser(session?.user ?? null);
    });

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      setUser(session?.user ?? null);
      if (session) {
        console.log('User logged in:', session.user);
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  // Load saved settings on mount
  useEffect(() => {
    const loadSavedSettings = async () => {
      try {
        // Load and apply saved filters
        const savedFilters = await loadFilters();
        if (savedFilters) {
          savedFilters.brands.forEach(brand => onFilterChange('brands', brand));
          savedFilters.series.forEach(series => onFilterChange('series', series));
          savedFilters.types.forEach(type => onFilterChange('type', type));
        }

        // Load and apply saved column settings
        const savedColumns = await loadColumns();
        if (savedColumns) {
          // Create a map of current column visibility
          const currentColumns = Object.fromEntries(
            columns.map(col => [col.id, col.isVisible])
          );

          // Update each column's visibility if it differs from saved state
          Object.entries(savedColumns).forEach(([columnId, isVisible]) => {
            if (currentColumns[columnId] !== isVisible) {
              onColumnVisibilityChange(columnId);
            }
          });
        }
      } catch (error) {
        console.error('Error loading settings:', error);
      }
    };

    loadSavedSettings();
  }, []);

  // Save filters when they change
  useEffect(() => {
    const saveFilterSettings = async () => {
      try {
        const filters: StoredFilters = {
          brands: selectedBrands,
          series: selectedSeries,
          types: selectedTypes,
        };
        await saveFilters(filters);
      } catch (error) {
        console.error('Error saving filter settings:', error);
      }
    };

    saveFilterSettings();
  }, [selectedBrands, selectedSeries, selectedTypes]);

  // Save column settings when they change
  useEffect(() => {
    const saveColumnSettings = async () => {
      try {
        const columnSettings: StoredColumns = {};
        columns.forEach(column => {
          columnSettings[column.id] = column.isVisible;
        });
        await saveColumns(columnSettings);
      } catch (error) {
        console.error('Error saving column settings:', error);
      }
    };

    saveColumnSettings();
  }, [columns]);

  const handleSignOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      setSession(null);
      setUser(null);
      setIsOpen(false);
    } catch (error) {
      Alert.alert('Error', 'Failed to sign out');
      console.error('Sign out error:', error);
    }
  };

  // Handle column visibility change
  const handleColumnVisibilityChange = async (columnId: string) => {
    onColumnVisibilityChange(columnId);
  };

  // Handle reset columns
  const handleResetColumns = async () => {
    onResetColumns();
    try {
      const columnSettings: StoredColumns = {};
      columns.forEach(column => {
        columnSettings[column.id] = column.isVisible;
      });
      await saveColumns(columnSettings);
    } catch (error) {
      console.error('Error resetting column settings:', error);
    }
  };

  // Filter series based on selected brands
  const availableSeries = useMemo(() => {
    if (selectedBrands.length === 0) {
      return filterOptions.series;
    }

    // Get all series from foils that match selected brands
    const seriesSet = new Set<string>();
    foils.forEach(foil => {
      if (selectedBrands.includes(foil.brand) && foil.series) {
        seriesSet.add(foil.series);
      }
    });

    return Array.from(seriesSet).sort();
  }, [filterOptions.series, selectedBrands, foils]);

  const renderAccountTab = () => {
    if (!session) {
      return (
        <View style={styles.contentWrapper}>
          <View style={styles.content}>
            <Text style={[styles.contentTitle, { color: colors.text.primary, marginBottom: 16 }]}>Sign In</Text>
            <GoogleSignIn onSuccess={() => {
              // Session will be updated automatically through the auth state change listener
              setIsOpen(false);
            }} />
          </View>
        </View>
      );
    }

    return (
      <View style={styles.contentWrapper}>
        <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollContent}>
          <View style={styles.content}>
            <View style={styles.userInfo}>
              {user?.user_metadata?.avatar_url && (
                <Image 
                  source={{ uri: user.user_metadata.avatar_url }} 
                  style={styles.avatarImage}
                />
              )}
              <View style={styles.userTextInfo}>
                <Text style={[styles.userName, { color: colors.text.primary }]}>
                  {user?.user_metadata?.full_name || user?.email}
                </Text>
                <Text style={[styles.userEmail, { color: colors.text.secondary }]}>
                  {user?.email}
                </Text>
              </View>
            </View>

            <View style={[styles.menuDivider, { backgroundColor: colors.border }]} />

            <View style={styles.section}>
              <Text style={[styles.sectionTitle, { color: colors.text.primary }]}>Quivers</Text>
              <Pressable
                style={[styles.menuButton, { backgroundColor: colors.background.secondary }]}
                onPress={() => {
                  setIsQuiversModalVisible(true);
                }}
              >
                <View style={styles.menuButtonContent}>
                  {Platform.OS === 'web' ? (
                    <Text style={{ fontSize: 20, color: colors.text.primary }}>🎯</Text>
                  ) : (
                    <Ionicons name="bookmark-outline" size={20} color={colors.text.primary} />
                  )}
                  <Text style={[styles.menuButtonText, { color: colors.text.primary }]}>Manage Quivers</Text>
                </View>
                <MaterialIcons name="chevron-right" size={20} color={colors.text.secondary} />
              </Pressable>
            </View>

            <View style={styles.section}>
              <Pressable
                style={[styles.signOutButton, { backgroundColor: colors.background.secondary }]}
                onPress={handleSignOut}
              >
                <View style={styles.menuButtonContent}>
                  {Platform.OS === 'web' ? (
                    <Text style={{ fontSize: 20, color: colors.text.primary }}>⇥</Text>
                  ) : (
                    <Ionicons name="log-out-outline" size={20} color={colors.text.primary} />
                  )}
                  <Text style={[styles.menuButtonText, { color: colors.text.primary }]}>Sign Out</Text>
                </View>
              </Pressable>
            </View>
          </View>
        </ScrollView>
      </View>
    );
  };

  return (
    <>
      <Pressable 
        onPress={() => setIsOpen(true)}
        style={[styles.iconButton, { backgroundColor: colors.background.secondary }]}
      >
        <MaterialIcons name="settings" size={24} color={colors.text.primary} />
      </Pressable>

      <Modal
        visible={isOpen}
        transparent
        animationType="fade"
        onRequestClose={() => setIsOpen(false)}
      >
        <Pressable 
          style={[styles.modalOverlay, { backgroundColor: isDark ? 'rgba(0, 0, 0, 0.7)' : 'rgba(0, 0, 0, 0.5)' }]}
          onPress={() => setIsOpen(false)}
        >
          <Pressable 
            style={[styles.modalContent, { backgroundColor: colors.background.primary }]}
            onPress={e => e.stopPropagation()}
          >
            <View style={[styles.modalHeader, { borderBottomColor: colors.border }]}>
              <Text style={[styles.modalTitle, { color: colors.text.primary }]}>Settings</Text>
              <Pressable 
                onPress={() => setIsOpen(false)}
                style={styles.closeButton}
              >
                <MaterialIcons name="close" size={24} color={colors.text.secondary} />
              </Pressable>
            </View>

            <View style={[styles.tabs, { borderBottomColor: colors.border }]}>
              <Pressable
                style={[styles.tab, activeTab === 'filters' && [styles.activeTab, { borderBottomColor: colors.primary }]]}
                onPress={() => setActiveTab('filters')}
              >
                <Text style={[
                  styles.tabText, 
                  { color: colors.text.secondary },
                  activeTab === 'filters' && [styles.activeTabText, { color: colors.primary }]
                ]}>
                  Filters {(selectedBrands.length > 0 || selectedSeries.length > 0 || selectedTypes.length > 0) && '•'}
                </Text>
              </Pressable>
              <Pressable
                style={[styles.tab, activeTab === 'columns' && [styles.activeTab, { borderBottomColor: colors.primary }]]}
                onPress={() => setActiveTab('columns')}
              >
                <Text style={[
                  styles.tabText,
                  { color: colors.text.secondary },
                  activeTab === 'columns' && [styles.activeTabText, { color: colors.primary }]
                ]}>
                  Columns {columns.some(col => !col.isVisible) && '•'}
                </Text>
              </Pressable>
              <Pressable
                style={[styles.tab, activeTab === 'appearance' && [styles.activeTab, { borderBottomColor: colors.primary }]]}
                onPress={() => setActiveTab('appearance')}
              >
                <Text style={[
                  styles.tabText,
                  { color: colors.text.secondary },
                  activeTab === 'appearance' && [styles.activeTabText, { color: colors.primary }]
                ]}>
                  Appearance
                </Text>
              </Pressable>
              <Pressable
                style={[styles.tab, activeTab === 'account' && [styles.activeTab, { borderBottomColor: colors.primary }]]}
                onPress={() => setActiveTab('account')}
              >
                <Text style={[
                  styles.tabText,
                  { color: colors.text.secondary },
                  activeTab === 'account' && [styles.activeTabText, { color: colors.primary }]
                ]}>
                  Account
                </Text>
              </Pressable>
            </View>

            {activeTab === 'filters' ? (
              <View style={styles.contentWrapper}>
                <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollContent}>
                  <View style={styles.content}>
                    <View style={styles.headerWithAction}>
                      <Text style={[styles.contentTitle, { color: colors.text.primary }]}>Active Filters</Text>
                      {(selectedBrands.length > 0 || selectedSeries.length > 0 || selectedTypes.length > 0) && (
                        <Pressable 
                          onPress={onClearFilters}
                          style={[styles.clearButton, { backgroundColor: colors.background.secondary }]}
                        >
                          <Text style={[styles.clearButtonText, { color: colors.text.secondary }]}>Clear All</Text>
                        </Pressable>
                      )}
                    </View>
                    <View style={styles.section}>
                      <Text style={[styles.sectionTitle, { color: colors.text.primary }]}>Brand</Text>
                      <View style={styles.chipContainer}>
                        {filterOptions.brands.map((brand) => (
                          <Pressable
                            key={brand}
                            style={[
                              styles.chip,
                              { backgroundColor: colors.background.secondary },
                              selectedBrands.includes(brand) && [styles.chipSelected, { backgroundColor: colors.primary }],
                            ]}
                            onPress={() => onFilterChange('brands', brand)}
                          >
                            <Text 
                              style={[
                                styles.chipText,
                                { color: colors.text.secondary },
                                selectedBrands.includes(brand) && [styles.chipTextSelected, { color: colors.text.inverse }],
                              ]}
                            >
                              {brand}
                            </Text>
                          </Pressable>
                        ))}
                      </View>
                    </View>

                    <View style={styles.section}>
                      <Text style={[styles.sectionTitle, { color: colors.text.primary }]}>Type</Text>
                      <View style={styles.chipContainer}>
                        {filterOptions.types.map((type) => (
                          <Pressable
                            key={type}
                            style={[
                              styles.chip,
                              { backgroundColor: colors.background.secondary },
                              selectedTypes.includes(type) && [styles.chipSelected, { backgroundColor: colors.primary }],
                            ]}
                            onPress={() => onFilterChange('type', type)}
                          >
                            <Text 
                              style={[
                                styles.chipText,
                                { color: colors.text.secondary },
                                selectedTypes.includes(type) && [styles.chipTextSelected, { color: colors.text.inverse }],
                              ]}
                            >
                              {type}
                            </Text>
                          </Pressable>
                        ))}
                      </View>
                    </View>

                    <View style={styles.section}>
                      <Text style={[styles.sectionTitle, { color: colors.text.primary }]}>Series</Text>
                      <View style={styles.chipContainer}>
                        {availableSeries.length > 0 ? (
                          availableSeries.map((series) => (
                            <Pressable
                              key={series}
                              style={[
                                styles.chip,
                                { backgroundColor: colors.background.secondary },
                                selectedSeries.includes(series) && [styles.chipSelected, { backgroundColor: colors.primary }],
                              ]}
                              onPress={() => onFilterChange('series', series)}
                            >
                              <Text 
                                style={[
                                  styles.chipText,
                                  { color: colors.text.secondary },
                                  selectedSeries.includes(series) && [styles.chipTextSelected, { color: colors.text.inverse }],
                                ]}
                              >
                                {series}
                              </Text>
                            </Pressable>
                          ))
                        ) : (
                          <Text style={[styles.noSeriesText, { color: colors.text.secondary }]}>
                            No series available for selected brands
                          </Text>
                        )}
                      </View>
                    </View>
                  </View>
                </ScrollView>
              </View>
            ) : activeTab === 'columns' ? (
              <View style={styles.contentWrapper}>
                <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollContent}>
                  <View style={styles.content}>
                    <View style={styles.headerWithAction}>
                      <Text style={[styles.contentTitle, { color: colors.text.primary }]}>Visible Columns</Text>
                      {columns.some(col => !col.isVisible) && (
                        <Pressable 
                          onPress={handleResetColumns}
                          style={[styles.clearButton, { backgroundColor: colors.background.secondary }]}
                        >
                          <Text style={[styles.clearButtonText, { color: colors.text.secondary }]}>Reset</Text>
                        </Pressable>
                      )}
                    </View>
                    <View style={styles.columnList}>
                      {columns.map(column => (
                        <Pressable
                          key={column.id}
                          style={styles.columnItem}
                          onPress={() => handleColumnVisibilityChange(column.id)}
                        >
                          <Text style={[styles.columnLabel, { color: colors.text.primary }]}>{column.label}</Text>
                          <View style={[
                            styles.checkbox,
                            { borderColor: colors.border },
                            column.isVisible && [styles.checkboxChecked, { backgroundColor: colors.primary, borderColor: colors.primary }]
                          ]}>
                            {column.isVisible && (
                              <MaterialIcons name="check" size={16} color={colors.text.inverse} />
                            )}
                          </View>
                        </Pressable>
                      ))}
                    </View>
                  </View>
                </ScrollView>
              </View>
            ) : activeTab === 'appearance' ? (
              <View style={styles.contentWrapper}>
                <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollContent}>
                  <View style={styles.content}>
                    <Text style={[styles.contentTitle, { color: colors.text.primary }]}>Theme Settings</Text>
                    <View style={styles.section}>
                      <Pressable
                        style={[styles.menuButton, { backgroundColor: colors.background.secondary }]}
                        onPress={toggleTheme}
                      >
                        <View style={styles.menuButtonContent}>
                          <MaterialIcons 
                            name={isDark ? 'dark-mode' : 'light-mode'} 
                            size={20} 
                            color={colors.text.primary} 
                          />
                          <Text style={[styles.menuButtonText, { color: colors.text.primary }]}>
                            {isDark ? 'Dark Mode' : 'Light Mode'}
                          </Text>
                        </View>
                      </Pressable>
                    </View>
                  </View>
                </ScrollView>
              </View>
            ) : (
              renderAccountTab()
            )}
          </Pressable>
        </Pressable>
      </Modal>

      <Modal
        visible={isQuiversModalVisible}
        transparent
        animationType="fade"
        onRequestClose={() => setIsQuiversModalVisible(false)}
      >
        <View 
          style={[
            styles.modalOverlay,
            { backgroundColor: 'rgba(0, 0, 0, 0.5)' }
          ]}
          onStartShouldSetResponder={() => true}
        >
          <View 
            style={[
              styles.modalContent,
              { backgroundColor: colors.background.primary }
            ]}
          >
            <QuiverManager
              selectedFoils={[]}
              onClose={() => setIsQuiversModalVisible(false)}
              initialTab="saved"
              standalone={true}
            />
          </View>
        </View>
      </Modal>
    </>
  );
}

