import { useMemo } from 'react';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';
import { groupFoilsByBrand, groupFoilsBySeries } from '../utils/dataTransform';

export function useFilteredFoils(foils: BaseProduct[], filters: FilterOptions) {
  const filterOptions = useMemo(() => {
    const brands = Array.from(new Set(foils.map(foil => foil.brand))).sort();
    
    // Group foils by brand
    const foilsByBrand = groupFoilsByBrand(foils);
    
    // Get series based on selected brands
    let availableSeries: string[] = [];
    if (filters.brands.length === 0) {
      // If no brands selected, show all series
      availableSeries = Array.from(new Set(foils.map(foil => foil.series))).sort();
    } else {
      // If brands are selected, only show series from those brands
      const filteredFoils = filters.brands.flatMap(brand => foilsByBrand[brand] || []);
      availableSeries = Array.from(new Set(filteredFoils.map(foil => foil.series))).sort();
    }

    const types = ['front', 'tail'];

    return { brands, series: availableSeries, types };
  }, [foils, filters.brands]);

  const filteredFoils = useMemo(() => {
    return foils.filter(foil => {
      const brandMatch = filters.brands.length === 0 || filters.brands.includes(foil.brand);
      const seriesMatch = filters.series.length === 0 || filters.series.includes(foil.series);
      const typeMatch = filters.type.length === 0 || filters.type.includes(foil.type);

      return brandMatch && seriesMatch && typeMatch;
    });
  }, [foils, filters.brands, filters.series, filters.type]);

  return {
    filteredFoils,
    filterOptions,
  };
} 