import { BaseProduct } from '../types/scraper';

export function formatMeasurement(value: number, unit: string): string {
  return `${value}${unit}`;
}

export function convertArea(cmSquared: number): number {
  return Math.round(cmSquared * 0.155); // Convert to square inches
}

export function calculateAspectRatio(wingspan: number, area: number): number {
  return Math.round((wingspan * wingspan / area) * 100) / 100;
}

export function groupFoilsByBrand(foils: BaseProduct[]): Record<string, BaseProduct[]> {
  return foils.reduce((acc, foil) => {
    if (!acc[foil.brand]) {
      acc[foil.brand] = [];
    }
    acc[foil.brand].push(foil);
    return acc;
  }, {} as Record<string, BaseProduct[]>);
}

export function groupFoilsBySeries(foils: BaseProduct[]): Record<string, BaseProduct[]> {
  return foils.reduce((acc, foil) => {
    if (!acc[foil.series]) {
      acc[foil.series] = [];
    }
    acc[foil.series].push(foil);
    return acc;
  }, {} as Record<string, BaseProduct[]>);
}

export function getUniqueValues(foils: BaseProduct[], key: keyof BaseProduct): string[] {
  return Array.from(new Set(foils.map(foil => foil[key] as string))).sort();
} 