export const routes = {
  browse: '/',
  comparison: '/comparison',
  quiver: '/quiver/:quiverId',
  terms: '/terms',
  privacy: '/privacy',
  contact: '/contact',
} as const;

// Add a helper function to clean routes for navigation
export const cleanRoute = (route: string) => route.slice(1); // Remove leading slash 

export const getRouteFromPath = (path: string) => {
  return path.startsWith('/') ? path : `/${path}`;
}; 