import React, { useState, useEffect, useRef } from 'react';
import { View, TextInput, StyleSheet, TouchableOpacity, Platform, Text, useWindowDimensions } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { useTheme } from '../theme/ThemeContext';
import { spacing } from '../theme/spacing';
import { MOBILE_BREAKPOINT } from '../constants/layout';

interface SearchBarProps {
  value: string;
  onChangeText: (text: string) => void;
  onClear: () => void;
  placeholder?: string;
  autoFocus?: boolean;
  mode?: 'browse' | 'comparison' | 'quiver';
  onFocus?: () => void;
}

export const SearchBar: React.FC<SearchBarProps> = ({
  value,
  onChangeText,
  onClear,
  placeholder = 'Search foils...',
  autoFocus = false,
  mode = 'browse',
  onFocus,
}) => {
  const { colors } = useTheme();
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;

  const getPlaceholder = () => {
    if (isMobile) {
      // Mobile-specific shorter placeholders
      switch (mode) {
        case 'comparison':
          return 'Search to add...';
        case 'quiver':
          return 'Search to add...';
        default:
          return 'Search foils...';
      }
    }

    // Desktop placeholders
    switch (mode) {
      case 'comparison':
        return 'Search foils to add to comparison...';
      case 'quiver':
        return 'Search foils to add to quiver...';
      default:
        return 'Search foils...';
    }
  };

  return (
    <View style={[styles.container, { backgroundColor: colors.background.secondary }]}>
      {Platform.OS === 'web' ? (
        <Text style={[styles.searchIcon, { color: colors.text.secondary }]}>🔍</Text>
      ) : (
        <Ionicons name="search" size={20} color={colors.text.secondary} />
      )}
      <TextInput
        style={[styles.input, { color: colors.text.primary }]}
        value={value}
        onChangeText={onChangeText}
        placeholder={getPlaceholder()}
        placeholderTextColor={colors.text.tertiary}
        autoFocus={autoFocus}
        onFocus={onFocus}
        autoCapitalize="none"
        autoCorrect={false}
        clearButtonMode="never"
        returnKeyType="search"
        maxFontSizeMultiplier={1}
        {...(Platform.OS === 'web' ? {
          spellCheck: false,
          autoComplete: 'off',
        } : {
          keyboardType: 'web-search',
        })}
      />
      {value !== '' && (
        <TouchableOpacity onPress={onClear} style={styles.clearButton}>
          {Platform.OS === 'web' ? (
            <Text style={[styles.clearIcon, { color: colors.text.secondary }]}>✕</Text>
          ) : (
            <Ionicons name="close-circle" size={20} color={colors.text.secondary} />
          )}
        </TouchableOpacity>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'center',
    paddingHorizontal: spacing.MEDIUM,
    height: 40,
    borderRadius: 8,
    gap: spacing.SMALL,
  },
  input: {
    flex: 1,
    fontSize: 16,
    height: '100%',
    ...(Platform.OS === 'web' && {
      outlineStyle: 'none',
      border: 'none',
      background: 'transparent',
      lineHeight: 40,
    }),
  },
  clearButton: {
    height: '100%',
    aspectRatio: 1,
    alignItems: 'center',
    justifyContent: 'center',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  searchIcon: {
    fontSize: 16,
    width: 20,
    height: 20,
    textAlign: 'center',
    lineHeight: 20,
  },
  clearIcon: {
    fontSize: 16,
    width: 20,
    height: 20,
    textAlign: 'center',
    lineHeight: 20,
  },
}); 